import * as React from "react";
import { Link } from "gatsby";
import { Separator } from "@/components/ui/separator";
import { cn } from "@/lib/utils";
import { Button } from "@/components/ui/button";
import { Sheet, SheetTrigger, SheetContent } from "@/components/ui/sheet";
import { Menu } from "lucide-react";

const links = [
    { to: "/", name: "Home" },
    { to: "/blog/tech", name: "Tech" },
    { to: "/blog/everything-else", name: "Everything else" },
    { to: "/about", name: "About" },
];

const Header = () => {
    const [open, setOpen] = React.useState(false);

    return (
        <div className={cn("flex items-center py-8 justify-between w-full")}>
            <Link to={"/"} className={cn("no-underline hover:opacity-100")}>
                <h2>{"balake"}</h2>
            </Link>
            {/* Desktop nav */}
            <div
                className={cn(
                    "space-x-4 flex-row items-center justify-between hidden md:flex",
                )}
            >
                {links.map(({ to, name }, i) => (
                    <React.Fragment key={to}>
                        <Link to={to}>{name}</Link>
                        {i !== links.length - 1 && (
                            <Separator
                                orientation="vertical"
                                className={cn("data-[orientation=vertical]:h-4")}
                            />
                        )}
                    </React.Fragment>
                ))}
            </div>
            {/* Mobile hamburger */}
            <div className="md:hidden">
                <Sheet open={open} onOpenChange={setOpen}>
                    <SheetTrigger asChild>
                        <Button variant="ghost" size="icon" aria-label="Open menu">
                            <Menu className="h-6 w-6" />
                        </Button>
                    </SheetTrigger>
                    <SheetContent side="right" className="flex flex-col gap-4 pt-12 px-4">
                        {links.map(({ to, name }) => (
                            <Link
                                key={to}
                                to={to}
                                className="text-lg py-2"
                                onClick={() => setOpen(false)}
                            >
                                {name}
                            </Link>
                        ))}
                    </SheetContent>
                </Sheet>
            </div>
        </div>
    );
};

export { Header };
